<template>
  <div id="login">
    <div style="width: 100%; height: 100px;"></div>
    <Row>
      <Col :xs="{span: 16, offset: 4}" :md="{span: 8, offset: 15}">
        <Card>
          <Form ref="loginValidate" :model="formValidate" :rules="ruleValidate" :label-width="80" onsubmit="return false">
            <span>
                <FormItem v-if="flag" label="用户名" prop="name">
                  <Input v-model="formValidate.name" placeholder="Enter your name">
                    <span slot="prepend">
                      <Icon :size="16" type="person"></Icon>
                    </span>
                      </Input>
                    </FormItem>
                    <FormItem v-else label="邮箱" prop="mail">
                      <Input v-model="formValidate.mail" placeholder="Enter your e-mail">
                    <span slot="prepend">
                      <Icon :size="16" type="email"></Icon>
                    </span>
                  </Input>
                </FormItem>
            </span>
            <span>
                <FormItem label="密码" prop="password">
                  <Input v-model="formValidate.password" placeholder="Enter your password" :type="showPassword ? 'text' : 'password'">
                    <span slot="prepend">
                      <Icon :size="16" type="locked"></Icon>
                    </span>
                        <span slot="append">
                      <Button style="padding: 3px 10px" @click="showPassword = !showPassword">
                        <Icon :size="18" style="color:#2D8CF0" type="eye" v-if="showPassword"></Icon>
                        <Icon :size="18" type="eye" v-else></Icon>
                      </Button>
                    </span>
                  </Input>
                </FormItem>
            </span>
            <span>
                <FormItem prop="remember">
                  <Checkbox v-model="formValidate.remember">记住我</Checkbox>
                  <span style="color:grey">请不要在公共电脑勾选此项</span>
                </FormItem>
            </span>
            <span>
              <span style="margin-left: 10%;">
                <Button v-if="flag" type="ghost" @click="switchLogin()">使用邮箱登录</Button>
                <Button v-else type="ghost" @click="switchLogin()">使用账号登录</Button>
              </span>
              <span style="margin-left: 4%;">
                <Button html-type="submit" type="primary" @click="Login()">登录</Button>
              </span>
              <span style="margin-left: 4%;">
                <Button type="ghost" @click="ToRegister()">立即注册</Button>
              </span>
            </span>
          </Form>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
import store from 'store'
import iView from 'iview'
export default {
  name: '',
  data () {
    return {
      status: 'false',
      showPassword: false,
      flag: true,
      State: [],
      formValidate: {
        name: '',
        mail: '',
        password: '',
        remember: false
      },
      ruleValidate: {
        name: [
          { required: true, message: '用户名不能为空', trigger: 'blur' },
          { pattern: /^[a-zA-Z][a-zA-Z0-9_]{3,19}$/, message: '用户名只能由4-20位的字母,数字,下划线组成', trigger: 'blur' },
          { pattern: /^[a-zA-Z]/, message: '用户名只能由字母开头', trigger: 'blur' }
        ],
        mail: [
          { required: true, message: '邮箱不能为空', trigger: 'blur' },
          { type: 'email', message: '不合法的邮箱格式', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' },
          { pattern: /^[\x21-\x7e]{4,20}$/, message: '密码长度必须在4到20位之间', trigger: 'blur' },
          { pattern: /^[a-zA-Z]/, message: '密码必须由字母开头', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
  },
  beforeMount: function () {
    this.$http.get('user/' + this.$store.state.auth.authUser, {params: {token: this.$store.state.auth.token}})
      .then((response) => {
        this.$Notice.success({title: '登陆成功'})
        this.$Loading.finish()
        this.$router.back()
      })
      .catch((e) => {
      })
    document.title = '登录' + window.title_suf
  },
  methods: {
    switchLogin () {
      this.flag = !this.flag
    },
    Login () {
      event.preventDefault()
      this.$refs.loginValidate.validate((valid) => {
        if (valid) {
          this.$Loading.start()
          let formData = new FormData()
          if (this.flag) {
            formData.append('username', this.formValidate.name)
          } else formData.append('username', this.formValidate.mail)
          let createHash = require('create-hash')
          let hash = createHash('sha1')
          hash.update(this.formValidate.password)
          this.formValidate.password = hash.digest('HEX')
          formData.append('password', this.formValidate.password)
          formData.append('remember', this.formValidate.remember)
          let config = {
            header: {
              'Content-Type': 'multipart/form-data'
            }
          }
          this.$http.post('auth/login', formData, config)
            .then(response => {
              localStorage.clear()
              store.set('token', response.data.token)
              store.set('authUser', response.data.user_id)
              this.$store.commit('SET_API_TOKEN', response.data.token)
              this.$store.commit('SET_AUTH_USER', response.data.user_id)
              this.$Loading.finish()
              this.$Notice.success({ title: '登录成功' })
              this.$router.push({ name: 'Index' })
            })
            .catch(e => {
              iView.LoadingBar.error()
              switch (e.response.status) {
                case 401:
                  if (this.flag === true) iView.Notice.error({title: '用户名或密码错误'})
                  else iView.Notice.error({title: '邮箱或密码错误'})
                  break
                case 404:
                  if (!this.flag) iView.Notice.error({title: '该邮箱未注册'})
                  else iView.Notice.error({title: '该用户名未注册'})
              }
              this.formValidate.password = ''
            })
        }
      })
    },
    ToRegister () {
      this.$Loading.start()
      this.$Loading.finish()
      this.$router.push({ name: 'Register' })
    }
  }
}
</script>

<style scoped>
  #login {
    height: 100vh;
    /*background: #FFEFDB;*/
    background-image: url("../../../../../title.jpg");
    background-repeat: no-repeat;
  }
</style>



